import React from 'react';
import useBEM from '../../utils/hooks/useBEM';

interface Props {
    circle?: boolean,
    background?: string,
    type: 'left' | 'right' | 'top' | 'bottom'
}

const Arrow : React.FC<Props>  = ({
    circle = false,
    background = '#fff',
    type
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('arrow');

    // HOOKS

    // METHODS
    const classModifiers = () => {
        const modifiers = []
        if(circle)modifiers.push('circle');
        modifiers.push(type);
        return modifiers;
    }

    const customStyle = () => ({
        backgroundColor: background
    })

    return (
        <div className={B(classModifiers())} style={customStyle()}>
            
        </div>
    )
}

export default Arrow;