import React, { useEffect, useState } from 'react';

interface Props {
    name: string,
    extension?: 'png' | 'jpg' | 'svg' | 'gif',
    className?: string,
}

const Image : React.FC<Props>  = ({
        name,
        className,
        extension = 'png'
    }) =>  {
    // VARIABLES
    const [ image, setImage ] = useState('');

    // METHODS

    const loadImage = () => {        
        import(`../../assets/images/${name}.${extension}`).then(img => {
          setImage(img.default);
        });
    };

    const renderImage = () => {
        return <img src={image} alt="" className={className}/>
    }

    // HOOKS
    useEffect(() => {
        loadImage();
    }, [name])

    return (
        <>
            { image && renderImage()}
        </>
    );
}

export default Image;