import React from 'react';
import iNeedExplorerQuestion from '../../interface/need-explorer-question.interface';
import iQuestionOption from '../../interface/question-option.interface';
import useBEM from '../../utils/hooks/useBEM';
import Image from '../ui/Image';

interface Props {
    question: iNeedExplorerQuestion,
    value?: iQuestionOption,
    onAnswer?: (answer: iQuestionOption) => void
}

const QuestionChoiceList : React.FC<Props>  = ({
    question,
    value,
    onAnswer
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('question-choice-list');

    // HOOKS

    // METHODS
    const modifierClass = (option: iQuestionOption) => {
        const modifiers = [];
        if(value?.value === option.value)modifiers.push('selected');
        if(option?.icon)modifiers.push('with-icon');
        if(question.canSelectMultiple)modifiers.push('multiple');
        return modifiers;
    }

    const onAnswerHandler = (option: iQuestionOption) => {
        if(onAnswer)onAnswer(option);
    }

    return (
        <div className={B()}>
            <h2 className={E('question')}>{question.question}</h2>

            <div className={E('options-container')}>
                <ul className={E('options')}>
                    {question.options?.map((option) => (
                        <li 
                            className={E('option', modifierClass(option))} 
                            onClick={() => onAnswerHandler(option)}                            
                            key={option.value}>
                            
                            <label>{option.label}</label>
                            
                            {
                                option.icon && 
                                <Image name={option.icon}/>
                            }

                        </li>
                    ))}
                </ul>
            </div>
        </div>
    )
}

export default QuestionChoiceList;